import {render} from "../app-utilities/utilities.js";
import Dialog from "../dialog/dialog";
import {api} from '@nn/nn-api';

export default class GDPROverview {
  constructor(element, templates) {
    this.element = element;
    this.templates = templates;
    this.dataSources = [];

    // Tab information
    this.data = {
      tabs: []
    };

    // Setup the dialog used to request additional information
    this.dialog = new Dialog({themes: ['light', 'l']});
  }

  /**
   * Initializes
   */
  init() {
    this.renderSpinner();

    this.data.tabs = [];
    return Promise.all(this.dataSources)
      .then(this.renderTabs.bind(this))
      .then(this._bindHandlers.bind(this))
      .then(() => this); // end by returning this instance
  }

  renderSpinner() {
    return render(this.element, {isLoading: true}, this.templates.main);
  }

  renderTabs(data) {
    this.data.tabs = this.data.tabs.concat(...data);
    return render(this.element, this.data, this.templates.main);
  }

  /**
   * Add new tab data to the dataset.
   *
   * @param {...object} dataSources, data to display;
   * @returns undefined.
   */
  addSources(...dataSources) {
    this.dataSources.push(...dataSources);
    return this;
  }

  /**
   * Bind the handlers for the information request form.
   */
  _bindHandlers() {
    const dialogButtons = [...this.element.querySelectorAll('[data-role="gdpr-extra-request"]')];
    dialogButtons.forEach(btn => {
      btn.addEventListener('click', this._dialogClickHandler.bind(this));
    });
  }

  _getReasonValue(elements) {
    let value;
    elements.forEach(element => {
      if (element.checked) {
        value = element.value;
      }
    });
    return value;
  }

  _dialogClickHandler(event) {
    event.preventDefault();

    const productCategory = event.target.getAttribute('data-category');
    const participations = JSON.parse(event.target.getAttribute('data-participations'));
    const reasons = JSON.parse(event.target.getAttribute('data-reasons'));
    const reason_other = event.target.getAttribute('data-reason-other') === 'true';
    const title = event.target.getAttribute('data-title');
    const intro = event.target.getAttribute('data-intro');
    const question = event.target.getAttribute('data-question');

    const dialogContext = this.dialog.setContent(this.templates.dialog({
      participations,
      reasons,
      reason_other,
      title,
      intro,
      question
    }));
    const dialogOtherReasonInput = dialogContext.querySelector('#client-reason');
    const dialogParticipationSelect = dialogContext.querySelector('#client-participation');
    const dialogReasonError = dialogContext.querySelector('#client-reason-error');
    const radioElements = [...dialogContext.querySelectorAll('[name="reason"]')];

    if (reason_other) {
      this._bindHandlersForReasonOther(radioElements, reasons, dialogOtherReasonInput);
    }

    dialogContext.querySelector('form').addEventListener('submit', (evt) => {
      evt.preventDefault();

      if(this.busy) {
        return;
      }

      const selectedIndex = this._getReasonValue(radioElements);
      if (!selectedIndex || (reason_other && selectedIndex === (reasons.length - 1).toString() && !dialogOtherReasonInput.value)) {
        return dialogReasonError.classList.remove('u-hidden');
      }

      let reason = reasons[selectedIndex];

      if (reason_other && reason === reasons[reasons.length - 1]) {
        // it's the last one, which is other
        reason = reason + dialogOtherReasonInput.value;
      }

      dialogReasonError.classList.add('u-hidden');

      const payload = {
        requestDetails: reason,
        requestCategory: productCategory
      };

      if (participations) {
        const index = dialogParticipationSelect.value;
        payload.participation = participations[index];
      }

      this.busy = true;
      api.customer.requestCustomerInfoForCustomer(payload).then(() => {
        evt.target.reset();
        this.dialog.setContent(this.templates.dialogDone());
        this.busy = false;
      })
        .catch(error => {
          this.dialog.setContent(this.templates.dialogDone({error}));
          this.busy = false;
        });
    });

    this.dialog.show();
  }

  _bindHandlersForReasonOther(radioElements, reasons, dialogOtherReasonInput) {
    radioElements.forEach(radioElement => {
      radioElement.addEventListener('click', event => {
        if (event.target.value === (reasons.length - 1).toString()) {
          dialogOtherReasonInput.removeAttribute('readonly');
        } else {
          dialogOtherReasonInput.setAttribute('readonly', 'readonly');
        }
      });
    });
  }
}
