import {api, config as apiConfig} from '@nn/nn-api';
import { get } from '@nn/psdl-utility/src/object-utils'
import {formatDateFromString} from '../datepicker/date.helpers';
import {activityConfig as config} from './gdprConfig';

apiConfig.setFetchMethod(fetch);
apiConfig.setFetchCredentials('same-origin');

function extractInfo(TK, sections) {
  const section = {
    info: []
  };

  for (const [label, value] of Object.entries(TK)) {
    if(config.translations[label]) {
      let propValue = value;
      // formate dates if necessary
      if(config.dateField[label]) {
        propValue = formatDateFromString(propValue);
      }
      section.info.push({
        label: config.translations[label],
        value: propValue
      });
    }
  }

  sections.push(section);
}

export default function gdprGetActivityForCustomerSource() {
  const dataSource = {
    title: config.title,
    description: config.description,
    sections: []
  };

  return api.customer.getActivityForCustomer().then(result => {
    if (parseInt(get(result, 'SF.AL.AANTAL')) <= 0) {
      return dataSource;
    }

    if (Array.isArray(result.SF.TK)) {
      result.SF.TK.forEach(TK => {
        extractInfo(TK, dataSource.sections);
      });
    } else {
      extractInfo(result.SF.TK, dataSource.sections);
    }

    // enable extra request dialog
    dataSource.extra_request = config.extra_request;
    dataSource.product_category = config.product_category;
    dataSource.dialogTitle = config.dialogTitle;
    dataSource.dialogIntro = config.dialogIntro;
    dataSource.question = config.question;
    dataSource.reasons = JSON.stringify(config.reasons);
    dataSource.reason_other = config.reason_other;

    return dataSource;
  })
    .catch(() => undefined)
}
