import { api, config as apiConfig } from '@nn/nn-api';
import { info } from "./gdprUtils";
import { isEmpty } from '@nn/psdl-utility/src/object-utils';
import { marketingDataConfig as config } from "./gdprConfig";

apiConfig.setFetchMethod(fetch);
apiConfig.setFetchCredentials('same-origin');

function capitalizeLabel(label) {
  return label.charAt(0).toUpperCase() + label.slice(1).toLowerCase();
}

function extractItems(items) {
  const section = {
    info: []
  };

  items.forEach(item => {
    section.info.push(info(capitalizeLabel(item.Label), item.Value));
  });

  return section;
}

export default function gdprGetMarketingDataForCustomer() {
  const dataSource = {
    title: config.title,
    description: config.description,
    sections: []
  };
  return api.customer.getMarketingDataForCustomer().then(result => {
    if (!result.Items.Item || isEmpty(result.Items.Item)) {
      // no result, return empty source
      return dataSource;
    }

    if (!Array.isArray(result.Items.Item)) {
      // only one item, convert to array
      result.Items.Item = new Array(result.Items.Item);
    }

    dataSource.sections.push(extractItems(result.Items.Item));
    return dataSource;
  }).catch(() => undefined);
}