import {api, config as apiConfig} from '@nn/nn-api';
import {info, asArray} from "./gdprUtils";
import {partyAgreementsConfig as config} from "./gdprConfig";

apiConfig.setFetchMethod(fetch);
apiConfig.setFetchCredentials('same-origin');

function extractInfo(agreement, sections) {
  const section = {
    title: agreement.Id,
    info: []
  };
  Object.keys(agreement).forEach(key => {
    if (config.translations[key]) {
      section.info.push(info(config.translations[key], agreement[key]));
    }
  });
  sections.push(section);
}


function extractAgreements(agreements) {
  const sections = [];

  agreements.forEach(agreement => {
    extractInfo(agreement, sections);
  });

  return sections;
}

function extractParticipations(agreements) {
  return agreements.map(agreement => ({
    PolicyNumber: agreement.Id,
    VTAID: agreement.AgreementType,
    ProductCode: agreement.ProductCode,
  }));
}

export default function gdprGetPartyAgreementsForCustomer() {
  const dataSource = {
    title: config.title,
    description: config.description,
    sections: []
  };
  return api.customer.getPartyAgreementsForCustomer()
    .then(result => {
      if (result.Body.Agreement) {
        dataSource.sections = extractAgreements(asArray(result.Body.Agreement));
      }

      // enable extra request dialog
      dataSource.extra_request = config.extra_request;
      dataSource.product_category = config.product_category;
      dataSource.dialogTitle = config.dialogTitle;
      dataSource.dialogIntro = config.dialogIntro;
      dataSource.reasons = JSON.stringify(config.reasons);
      dataSource.reason_other = config.reason_other;

      // extra info only for products
      dataSource.participations = JSON.stringify(extractParticipations(asArray(result.Body.Agreement)));

      return dataSource;
    })
    .catch(() => undefined)
}
