import moment from 'moment';
import {blueconicConfig as config} from './gdprConfig';

/**
 * Blueconic GDPR data retrieval.
 *
 * This function asynchronously retrieves GDPR data from Blueconic using the
 * connector provided.
 *
 * The Blueconic Connector returns the properties from the Blueconic Client.
 * Don't mix up the terminology; a Blueconic "property" is a Blueconic concept.
 * The property is returned as a JS object, so "property" has nothing to do with
 * a JS object "property". Blueconic Properties will look something like this:
 *
 * "property_name": {
 *     "permissionLevel": "SOME_PERMISSION_LEVEL",
 *     "labels": {
 *         "en_US": "mostly equal to nl_NL, but probably unused",
 *         "DO_NOT_USE_WITHOUT_PERMISSION": "see en_US",
 *         "nl_NL": "the actual label to print for this property"
 *     }
 * }
 *
 * The value of "nl_NL" represents the name of the Blueconic property. However,
 * at the time of writing we there is no mechanism for passing metadata these
 * properties from the Blueconic dashboard to the frontend. Therefore,
 * per-property metadata is currently encoded in a prefix on the "nl_NL" (Dutch)
 * label. This is not exactly ideal, but it's the only channel we have to
 * actively communicate variables like sorting order, grouping, and formatting
 * for the GDPR overview. We didn't want to hardcode too much, so we decided to
 * use a simple DSL that can be parsed by a regexp.
 *
 * For the base case, the prefix starts with GDPR, followed by a numeric sort ID
 * that controls the order in which properties are displayed (we sort in
 * ascending order), followed by a numeric category ID that maps to a section,
 * followed by at least a single whitespace character. The prefix is removed
 * from the output.
 *
 * Property label: `GDPR-${sortId}-${categoryId} `,
 * e.g. "GDPR-004-02 Operating systeem versie (meest recent)",
 * which is sorted into the fourth position the second category and labelled as
 * "Operating systeem versie (meest recent)".
 *
 * An optional format specifier is also supported in the form of a letter:
 *
 * Property label: `GDPR-${sortId}-${categoryId}-${format} `
 * e.g. "GDPR-007-03-T Laatste bezoekdatum",
 * which is sorted into the seventh position of the third category, and
 * formatted as a timestamp. Note that the 'T' is the only format specifier we
 * need at the moment.
 *
 * The property, as returned by the Blueconic Client, should look like this:
 *
 * "lastvisitdate": {
 *     "permissionLevel": "ANONYMOUS",
 *     "labels": {
 *         "en_US": "GDPR-007-03-T Laatste bezoekdatum",
 *         "DO_NOT_USE_WITHOUT_PERMISSION": "GDPR-007-03 Laatste bezoekdatum",
 *         "nl_NL": "GDPR-007-03-T Laatste bezoekdatum"
 *     }
 * }
 *
 * Section names are dynamic can be altered from the Blueconic dashboard by
 * adding a property where the nl_NL label contains the following prefix:
 * 'GDPR-SECTION-${categoryId} ', that is, a fixed string, followed by a
 * category ID, followed by at least a single whitespace character,
 * e.g. 'GDPR-SECTION-03 Bezoek', where the third section is labelled "Bezoek".
 *
 * The associated Blueconic property should not be assigned a value (it's a
 * workaround). The Blueconic Client should return an object like this:
 *
 * "gdpr-section-03_bezoek": {
 *     "permissionLevel": "ANONYMOUS",
 *     "labels": {
 *         "en_US": "GDPR-SECTION-03 Bezoek",
 *         "DO_NOT_USE_WITHOUT_PERMISSION": "GDPR-SECTION-03 Bezoek",
 *         "nl_NL": "GDPR-SECTION-03 Bezoek"
 *     }
 * }
 *
 * @param {BlueconicConnector} bcConnector, the Blueconic Connector instance
 * we'll use to connect to talk to the Blueconic Client object. It's injected
 * so it can be mocked during testing.
 * @return {Promise.Object}, returns a promise that resolves to a GDPR data
 * object.
 */
export default function gdprBlueconicSource (bcConnector) {
  let bcProps;
  let gdprKeys;
  let sectionLabels;

  if(!bcConnector) {
    return Promise.resolve(undefined);
  }

  return bcConnector.getProfileProperties()
    .then(props => {
      bcProps = props;

      const keys = Object.keys(props)
        .filter(key => {
          return /^GDPR/.test(props[key].labels.nl_NL);
        });

      gdprKeys = keys
        .filter(key => {
          return /^GDPR-\d+-\d+(-[a-zA-Z]{1}){0,1}\s{1}/.test(props[key].labels.nl_NL);
        });

      sectionLabels = keys
        .reduce((labels, key) => {
          const rawLabel = bcProps[key].labels.nl_NL;
          const match = /^GDPR-SECTION-(\d+)\s{1}/.exec(rawLabel);
          if (match) {
            const label = rawLabel.substr(match[0].length).trim();
            const categoryId = parseInt(match[1] - 1);
            labels[categoryId] = label;
          }
          return labels;
        }, []);

      return bcConnector.loadValues(gdprKeys);
    })
    .then(() => {
      const output = {
        title: config.title,
        description: config.description,
        sections: []
      };

      const profile = bcConnector.getProfile();

      for (const key of gdprKeys) {
        const rawLabel = bcProps[key].labels.nl_NL;
        const match = /^GDPR-(\d+)-(\d+)(?:-([a-zA-Z]{1})){0,1}\s{1}/.exec(rawLabel);
        if (!match) {
          continue;
        }

        const label = rawLabel.substr(match[0].length).trim();
        const sortId = parseInt(match[1]);
        let categoryId = parseInt(match[2] - 1);
        const format = match[3];

        // If no category ID was provided we don't know where to put it.
        // However, we can't afford to skip it, so we lump it under
        // a misc. section.
        if (isNaN(categoryId)) {
          categoryId = 0;
        }

        let value = profile.getValues(key)
          .map(value => value.trim())
          .filter(value => value.length)
          .join(', ');

        if (!value) {
          continue
        }

        // Handle the format specifiers.
        if (format === 'T' && !isNaN(value)) {
          value = moment(parseInt(value)).format('D MMMM YYYY, kk:mm:ss');
        }

        if (!output.sections[categoryId]) {
          let title = sectionLabels[categoryId];
          if (!title) {
            title = '';
          }

          output.sections[categoryId] = {
            title,
            info: []
          };
        }

        output.sections[categoryId].info.push({
          label,
          value,
          sortId
        });
      }

      output.sections.forEach(section => {
        section.info = section.info
          .sort((a, b) => a.sortId - b.sortId)
          .map(({label, value}) => ({label, value}));
      });

      output.extra_request = config.extra_request;
      output.product_category = config.product_category;

      return output;
    })
    .catch(() => undefined);
}
