import {api, config as apiConfig} from '@nn/nn-api';
import { get } from '@nn/psdl-utility/src/object-utils'
import {formatDateFromString} from '../datepicker/date.helpers';
import {info, asArray} from "./gdprUtils";
import {businessPartnerDataConfig as config} from "./gdprConfig";

apiConfig.setFetchMethod(fetch);
apiConfig.setFetchCredentials('same-origin');

function extractInfo(data, section) {
  data = asArray(data);

  Object.keys(data).forEach(key => {
    Object.keys(data[key]).forEach((propKey, _, collection) => {
      let propValue = data[key][propKey];

      if (config.translations[propKey]) {
        let label = config.translations[propKey];

        // formate dates if necessary
        if (config.dateField[propKey]) {
          propValue = formatDateFromString(propValue);
        }

        // exception for email label
        if (config.formatEmailLabel[propKey]) {
          label = label + ' ' + config.formatEmailLabel.Usage[collection.Usage];
        }

        section.info.push(info(label, propValue));
      }
    });

    if (key < data.length - 1) {
      section.info.push({break: true});
    }
  });
}

function extractData(prop, data, subProp) {
  const extracted = asArray(get(data, prop));
  if (!extracted) {
    // no data found
    return;
  }

  const section = {
    info: []
  };

  extracted.forEach((dataProp, index) => {
    if (subProp) {
      dataProp = dataProp[subProp];
    }

    extractInfo(dataProp, section);

    if (index < extracted.length - 1) {
      section.info.push({break: true});
    }
  });

  return section;
}

function extractOptInformation(optInformation) {
  if (!optInformation) {
    // no data found
    return;
  }

  optInformation = asArray(optInformation);

  const section = {
    info: []
  };

  optInformation.forEach(optInfo => {
    if (config.optInformation.CommunicationUsage.indexOf(optInfo.CommunicationUsage) > -1) {
      const label = optInfo[config.optInformation.label];
      const value = optInfo[config.optInformation.value];
      section.info.push(info(label, value));
    }
  });

  return section;
}

export default function gdprGetBusinessPartnerDataForCustomer() {
  const dataSource = {
    title: config.title,
    description: config.description,
    sections: []
  };
  return api.customer.getBusinessPartnerDataForCustomer()
    .then(result => {
      const data = result.Party;
      if (typeof data !== 'object') {
        // no result, return empty source
        return dataSource;
      }

      dataSource.sections.push(extractData('Person.PersonName', data));
      dataSource.sections.push(extractData('Person.Demographics', data));
      dataSource.sections.push(extractData('PostalAddressContactPointUsage', data, 'PostalAddress'));
      dataSource.sections.push(extractData('MobilePhoneContactPointUsage', data, 'MobilePhone'));
      dataSource.sections.push(extractData('TelephoneNumberContactPointUsage', data, 'TelephoneNumber'));
      dataSource.sections.push(extractData('EmailAddressContactPointUsage', data, 'EmailAddress'));

      // Special case for OptInformation
      dataSource.sections.push(extractOptInformation(data.OptInformation));

      dataSource.extra_request = config.extra_request;
      dataSource.product_category = config.product_category;

      return dataSource;
    })
    .catch(() => undefined) // FIXME
}